import axios from 'axios';
import { Report } from 'notiflix/build/notiflix-report-aio';

const API_KEY = '38927636-b5f6066692f15e9e72ed3611e';

let isFirstSearch = true; //     

//    HTTP-  API Pixabay
export async function fetchImages(query, page) {
  try {
    const response = await axios.get('https://pixabay.com/api/', {
      params: {
        key: API_KEY,
        q: query,
        image_type: 'photo',
        orientation: 'horizontal',
        safesearch: true,
        page,
        per_page: 40,
      },
    });
    return response.data; //    ,  totalHits
  } catch (error) {
    throw error; //     
  }
}

//      
export async function fetchAndRenderImages(
  query,
  page,
  renderFunction,
  gallery,
  loadMoreBtn
) {
  try {
    const responseData = await fetchImages(query, page); //    
    const images = responseData.hits; //   
    if (images.length === 0) {
      Report.failure(
        'Sorry, there are no images matching your search query. Please try again.'
      ); // ,   
      loadMoreBtn.classList.add('hidden');
      return;
    }

    renderFunction(images, gallery);
    loadMoreBtn.classList.remove('hidden');
    const { height: cardHeight } =
      gallery.firstElementChild.getBoundingClientRect();
    window.scrollBy({
      top: cardHeight * 2,
      behavior: 'smooth',
    });

    //         ,  
    if (isFirstSearch || query !== previousSearchQuery) {
      Report.success(`Hooray! We found ${responseData.totalHits} images.`);
      isFirstSearch = false;
      previousSearchQuery = query; //   
    }
  } catch (error) {
    Report.failure('Error fetching images:', error);
  }
}

//       
export function renderImages(images, gallery) {
  const galleryMarkup = images
    .map(
      image => `
    <div class="photo-card">
      <a href="${image.largeImageURL}" data-lightbox="gallery" data-title="${image.tags}">
        <img src="${image.webformatURL}" alt="${image.tags}" loading="lazy" />
      </a>
      <div class="info">
        <p class="info-item">
          <b>Likes:</b> ${image.likes}
        </p>
        <p class="info-item">
          <b>Views:</b> ${image.views}
        </p>
        <p class="info-item">
          <b>Comments:</b> ${image.comments}
        </p>
        <p class="info-item">
          <b>Downloads:</b> ${image.downloads}
        </p>
      </div>
    </div>
  `
    )
    .join('');
  gallery.insertAdjacentHTML('beforeend', galleryMarkup); //    
  const lightbox = new SimpleLightbox('.gallery a', {
    captions: true,
    captionsData: 'alt',
  });
  lightbox.refresh(); //  lightbox   
}
